//   font-size: calc(24px + 67 * ((100vw - 320px) / (1350 - 320))); адаптивный шрифт

.btn {
  display: inline-flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: var(--font-primary);
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  border-radius: 5px;
  padding: 1.3rem 3.2rem;
  border: 3px solid var(--color-black);

  &.primary {
    background-color: var(--color-black);
    color: var(--color-white);
    margin-bottom: 1rem;

    @include desktop {
      &:hover {
        background-color: #e33327;
        border: 3px solid #e33327;
      }
    }
  }

  &.secondary {
    background-color: var(--color-white);
    color: var(--color-black);
    padding: 1.3rem 3.5rem;

    @include mobile {
      margin-left: 2.1rem;
    }

    @include desktop {
      &:hover {
        background-color: #e33327;
        border: 3px solid #e33327;
        color: var(--color-white);
      }
    }
  }
}

.section-title {
  font-family: var(--font-secondary);
  font-size: 7rem;
  line-height: 3rem;
  font-weight: 400;
  color: var(--color-black);

  @include tablet {
    font-size: 15rem;
    line-height: 13rem;
  }
}
